<template>
  <div>
    <h3 style="float: left; margin-left: 30px">杀软识别</h3>
    <a-divider/>
    <!--        输入框-->
    <div style="width: 330px; float:left; margin-left: 1%">
      <a-form-model
          ref="antivirusForm"
          :model="antivirus_form"
          :rules="antivirus_rules"
      >
        <a-form-model-item ref="values" label="" prop="values">
          <a-textarea style="overflow: auto" id="13"
                      v-model="antivirus_form.values" @blur="() => {$refs.values.onFieldBlur();}"
                      placeholder="在操作系统输入tasklist,粘贴到文本框即可"
                      :auto-size="{ minRows: 6, maxRows: 6 }"
          />
        </a-form-model-item>
      </a-form-model>
      <div style="float: left; margin-left: 30px; margin-top: -12px; text-align: left; line-height: 250%;">

        <a-button type="danger" @click="antivirus_search">
          提交
        </a-button>
      </div>

    </div>
    <!-- 说明 -->
    <div style="float: right; width: 260px; text-align: left; margin-right: 1%">
      <span>说明:</span>
      <br>
      &ensp;&ensp;通过查询当前系统的进程信息,快速识别杀软信息。
      在操作系统输入<font color="red">tasklist</font>,粘贴到文本框即可，如：
      <div style="text-indent: 0em; line-height: 20px; margin-top: 8px;">
            <span><font
                color="red">映像名称&ensp;&ensp; &ensp;  &ensp;PID &ensp;  &ensp; &ensp;服务</font></span><br>
        <span><font color="red">======&ensp;&ensp; &ensp;  &ensp;===&ensp;&ensp;  &ensp;===</font></span><br>
        <!--            <span style="margin-left: 28px">svchost.exe   1760   Audiosrv</span><br>-->
        <!--            <span style="margin-left: 28px">svchost.exe   1848   DusmSvc</span><br>-->
        <span><font color="red">firesvc.exe  &ensp;  &ensp; 1860 &ensp;&ensp;  McAfee</font></span><br>
      </div>

    </div>
    <!-- 展示界面 -->
    <div style="margin-top: 230px">
      <a-table :columns="antivirus_columns" :data-source="antivirus_data" :pagination="false"
               :style="'border-collapse: collapse; margin: 10px 10px; '" :key="1"
      >
            <span slot="name" slot-scope="text, record">
                  <a-tag color="green">{{ record.name }}</a-tag>
            </span>


      </a-table>

    </div>


  </div>
</template>



<script>

const antivirus_columns = [
  {title: '进程名称', dataIndex: 'process', key: 'process'},
  {title: '识别结果', dataIndex: 'name', key: 'name', scopedSlots: {customRender: 'name'}},
]

const antivirus_data = []


for (let i = 0; i < 0; ++i) {
  antivirus_data.push({
    key: i,
    process_name: '',
    recognition_result: ''})


}



export default {
  name: 'Antivirus',

  // components: {ShellList},
  data() {

    return {
      // 杀软识别的展示列及数据
      antivirus_data,
      antivirus_columns,
      // 杀软识别输入信息验证规则
      antivirus_form: {
        values: '',
      },
      antivirus_rules: {
        values: [{required: true, message: '填写内容不能为空', trigger: 'blur'}],
      },

      fullHeight: document.documentElement.clientHeight,
      fullWidth: document.documentElement.clientWidth,

    }
  },

  mounted() {

  },

  methods: {
    // 杀软识别
    antivirus_search() {
      this.$refs.antivirusForm.validate(valid => {
        if (!valid) {
          return;
        } else {
          this.$http.post('/antivirusRecognition', {task_list: this.antivirus_form.values}).then(res => {
            // console.log(res.data)
            if (res.data.status === true) {
              this.antivirus_data = res.data.data
              this.$message.success(res.data.msg)
            } else {
              this.$message.error(res.data.msg)
            }
          })
        }
      })
    },

  },

  created() {

    this.overtimer = window.setInterval(() => {
      setTimeout(() => {
        // console.log(document.documentElement.clientHeight, "antivirus ")
        this.fullHeight = document.documentElement.clientHeight
        this.fullWidth = document.documentElement.clientWidth
      })

    }, 1000)
    this.$once('hook:beforeDestroy', () => {
      clearInterval(this.overtimer);
      this.overtimer = null;
    })

  },

}
</script>



